<template>
  <Lay :route="route" :arr="arr" :url="url"/>
</template>

<script>
import Lay from '@/components/common/template';
import {welldata} from '@/config/well'
export default {
  props: ["route"],
  data () {
    return {
      url: '/data/query/targetdata',
      arr: welldata
    }
  },

  components: {
    Lay
  },

  computed: {},

  mounted: function() {

  },

  methods: {

  }
}
</script>


